import React from 'react';
import { throttle } from 'lodash-es';

const THROTTLE_TIME = 300;

export default function (callback) {
  React.useEffect(() => {
    const scrollCallback = throttle(() => {
      callback();
    }, THROTTLE_TIME);
    window.document.addEventListener('scroll', scrollCallback);
    window.addEventListener('resize', scrollCallback);
    return () => {
      window.document.removeEventListener('scroll', scrollCallback);
      window.removeEventListener('resize', scrollCallback);
    };
  }, [callback]);
}
